<template><section>
<h2 id="alert-ti-shi"><a class="header-anchor" href="#alert-ti-shi" aria-hidden="true">¶</a> Alert 提示</h2>
<p>提示信息弹框，包含4种结果弹框，不会自动关闭。</p>
<h3 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true">¶</a> 基础用法</h3>
<p>用于提示的信息展示弹框，有4中不同结果的弹框。。</p>
<demo-block class="demo-box">
                      <div class="source" slot="source"><py-alert type="info">默认提示信息</py-alert>
<py-alert type="success">成功提示信息</py-alert>
<py-alert type="warning">警告提示信息</py-alert>
<py-alert type="error">失败提示信息</py-alert>
</div>
                      <div class="description"><p>type 接收 info、success、warning 和 error 四种字符串类型。</p>
</div>
                      <div class="highlight" slot="highlight">
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">py-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span>&gt;</span>默认提示信息<span class="hljs-tag">&lt;/<span class="hljs-name">py-alert</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">py-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span>&gt;</span>成功提示信息<span class="hljs-tag">&lt;/<span class="hljs-name">py-alert</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">py-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"warning"</span>&gt;</span>警告提示信息<span class="hljs-tag">&lt;/<span class="hljs-name">py-alert</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">py-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"error"</span>&gt;</span>失败提示信息<span class="hljs-tag">&lt;/<span class="hljs-name">py-alert</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="zi-ding-yi-guan-bi-an-niu"><a class="header-anchor" href="#zi-ding-yi-guan-bi-an-niu" aria-hidden="true">¶</a> 自定义关闭按钮</h3>
<p>可以设置是否显示关闭按钮，或以文字代替关闭按钮。</p>
<demo-block class="demo-box">
                      <div class="source" slot="source"><py-alert type="info" :closable="false">默认提示信息</py-alert>
<py-alert type="success">成功提示信息</py-alert>
<py-alert type="warning" close-text="关闭">警告提示信息</py-alert>
<py-alert type="error" close-text="我知道了">失败提示信息</py-alert>
</div>
                      <div class="description"><p>定义 closable 为 Boolean 类型，用于设置是否显示关闭按钮，close-text 为String 类型，用于设置替代关闭按钮的文字。</p>
</div>
                      <div class="highlight" slot="highlight">
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">py-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">:closable</span>=<span class="hljs-string">"false"</span>&gt;</span>默认提示信息<span class="hljs-tag">&lt;/<span class="hljs-name">py-alert</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">py-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span>&gt;</span>成功提示信息<span class="hljs-tag">&lt;/<span class="hljs-name">py-alert</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">py-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"warning"</span> <span class="hljs-attr">close-text</span>=<span class="hljs-string">"关闭"</span>&gt;</span>警告提示信息<span class="hljs-tag">&lt;/<span class="hljs-name">py-alert</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">py-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"error"</span> <span class="hljs-attr">close-text</span>=<span class="hljs-string">"我知道了"</span>&gt;</span>失败提示信息<span class="hljs-tag">&lt;/<span class="hljs-name">py-alert</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="dai-biao-ti"><a class="header-anchor" href="#dai-biao-ti" aria-hidden="true">¶</a> 带标题</h3>
<p>显示标题提示信息。</p>
<demo-block class="demo-box">
                      <div class="source" slot="source"><py-alert type="info" title="提示">默认提示信息</py-alert>
<py-alert type="success" title="提示" :is-center="true">成功提示信息</py-alert>
<py-alert type="warning" close-text="关闭" :is-center="true">警告提示信息</py-alert>
</div>
                      <div class="description"><p>title 接收String类型作为标题，is-center设置为true时，文字居中显示。</p>
</div>
                      <div class="highlight" slot="highlight">
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">py-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"提示"</span>&gt;</span>默认提示信息<span class="hljs-tag">&lt;/<span class="hljs-name">py-alert</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">py-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"提示"</span> <span class="hljs-attr">:is-center</span>=<span class="hljs-string">"true"</span>&gt;</span>成功提示信息<span class="hljs-tag">&lt;/<span class="hljs-name">py-alert</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">py-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"warning"</span> <span class="hljs-attr">close-text</span>=<span class="hljs-string">"关闭"</span> <span class="hljs-attr">:is-center</span>=<span class="hljs-string">"true"</span>&gt;</span>警告提示信息<span class="hljs-tag">&lt;/<span class="hljs-name">py-alert</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="dai-tu-biao-biao-ti"><a class="header-anchor" href="#dai-tu-biao-biao-ti" aria-hidden="true">¶</a> 带图标标题</h3>
<demo-block class="demo-box">
                      <div class="source" slot="source"><py-alert type="info" title="提示" :title-icon="true">默认提示信息</py-alert>
<py-alert type="success" title="提示" :is-center="true" :title-icon="true">成功提示信息</py-alert>
</div>
                      <div class="description"><p>title-icon 接收Boolean类型，值为true时标题显示图标。</p>
</div>
                      <div class="highlight" slot="highlight">
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">py-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"提示"</span>  <span class="hljs-attr">:title-icon</span>=<span class="hljs-string">"true"</span>&gt;</span>默认提示信息<span class="hljs-tag">&lt;/<span class="hljs-name">py-alert</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">py-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"提示"</span> <span class="hljs-attr">:is-center</span>=<span class="hljs-string">"true"</span>  <span class="hljs-attr">:title-icon</span>=<span class="hljs-string">"true"</span>&gt;</span>成功提示信息<span class="hljs-tag">&lt;/<span class="hljs-name">py-alert</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="dai-tu-biao-wen-zi"><a class="header-anchor" href="#dai-tu-biao-wen-zi" aria-hidden="true">¶</a> 带图标文字</h3>
<demo-block class="demo-box">
                      <div class="source" slot="source"><py-alert type="error" title="提示" :show-icon="true">
  <p>发生错误了</p>
  <p>请尽快解决</p>
</py-alert>
</div>
                      <div class="description"><p>show-icon 接收Boolean类型，值为true时显示辅助性图标。</p>
</div>
                      <div class="highlight" slot="highlight">
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">py-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"error"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"提示"</span> <span class="hljs-attr">:show-icon</span>=<span class="hljs-string">"true"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>发生错误了<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>请尽快解决<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">py-alert</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="bu-tong-zi-hao-da-xiao"><a class="header-anchor" href="#bu-tong-zi-hao-da-xiao" aria-hidden="true">¶</a> 不同字号大小</h3>
<demo-block class="demo-box">
                      <div class="source" slot="source"><py-alert type="info">默认字号</py-alert>
<py-alert type="info" size="medium">medium字号</py-alert>
<py-alert type="info" size="large">large字号</py-alert>
</div>
                      <div class="description"><p><strong>size</strong> 接收String类型，值为 <strong>medium</strong> 时显示中号字号，值为 <strong>large</strong> 时显示大号字号。</p>
</div>
                      <div class="highlight" slot="highlight">
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">py-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span>&gt;</span>默认字号<span class="hljs-tag">&lt;/<span class="hljs-name">py-alert</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">py-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"medium"</span>&gt;</span>medium字号<span class="hljs-tag">&lt;/<span class="hljs-name">py-alert</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">py-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"large"</span>&gt;</span>large字号<span class="hljs-tag">&lt;/<span class="hljs-name">py-alert</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="guan-bi-shi-jian"><a class="header-anchor" href="#guan-bi-shi-jian" aria-hidden="true">¶</a> 关闭事件</h3>
<demo-block class="demo-box">
                      <div class="source" slot="source"><py-alert type="info" @close="closeAlert" close-text="关闭">关闭alert</py-alert>


</div>
                      <div class="description"><p>show-icon 接收Boolean类型，值为true时显示辅助性图标。</p>
</div>
                      <div class="highlight" slot="highlight">
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">py-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span> <span class="hljs-attr">@close</span>=<span class="hljs-string">"closeAlert"</span> <span class="hljs-attr">close-text</span>=<span class="hljs-string">"关闭"</span>&gt;</span>关闭alert<span class="hljs-tag">&lt;/<span class="hljs-name">py-alert</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    closeAlert() {
      alert(<span class="hljs-string">'您正在关闭alert'</span>);
    }
  },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="attributes"><a class="header-anchor" href="#attributes" aria-hidden="true">¶</a> Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>提示类型</td>
<td>string</td>
<td>info/success/warning/error</td>
<td>—</td>
</tr>
<tr>
<td>closable</td>
<td>是否显示关闭提示按钮</td>
<td>boolean</td>
<td>true/false</td>
<td>true</td>
</tr>
<tr>
<td>close-text</td>
<td>定义关闭按钮文字</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>title</td>
<td>提示信息标题</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>is-center</td>
<td>是否居中显示</td>
<td>boolean</td>
<td>true/false</td>
<td>false</td>
</tr>
<tr>
<td>show-icon</td>
<td>是否显示辅助性图标</td>
<td>boolean</td>
<td>true/false</td>
<td>false</td>
</tr>
<tr>
<td>title-icon</td>
<td>是否显示标题图标</td>
<td>boolean</td>
<td>true/false</td>
<td>false</td>
</tr>
<tr>
<td>size</td>
<td>设置文字大小</td>
<td>string</td>
<td>medium/large</td>
<td>—</td>
</tr>
</tbody>
</table>
<h3 id="events"><a class="header-anchor" href="#events" aria-hidden="true">¶</a> Events</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>类型</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>点击关闭按钮触发</td>
<td>—</td>
</tr>
</tbody>
</table>
</section></template>
<style>
  .py-alert{
    margin-bottom: 10px;
  }
</style>
<script>
export default {
  methods: {
    closeAlert() {
      alert('您正在关闭alert');
    }
  },
};
</script>