<template><section><h2 id="input-shu-ru-kuang"><a class="header-anchor" href="#input-shu-ru-kuang" aria-hidden="true">¶</a> Input 输入框</h2>
<p>通过鼠标或键盘输入字符。</p>
<h3 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true">¶</a> 基础用法</h3>
<demo-block class="demo-box">
                      <div class="source" slot="source"><py-input v-model="input" placeholder="请输入内容"></py-input>


</div>
                      <div class="description"></div>
                      <div class="highlight" slot="highlight">
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">py-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">py-input</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">input</span>: <span class="hljs-string">''</span>,
    };
  },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="jin-yong-zhuang-tai"><a class="header-anchor" href="#jin-yong-zhuang-tai" aria-hidden="true">¶</a> 禁用状态</h3>
<demo-block class="demo-box">
                      <div class="source" slot="source"><py-input v-model="input1" disabled placeholder="请输入内容">
</py-input>


</div>
                      <div class="description"><p>通过 <code>disabled</code> 属性指定是否禁用 Input 组件</p>
</div>
                      <div class="highlight" slot="highlight">
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">py-input</span>
  <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input1"</span>
  <span class="hljs-attr">disabled</span>
  <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span>
&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">py-input</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">input1</span>: <span class="hljs-string">''</span>,
    };
  },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="wen-ben-yu"><a class="header-anchor" href="#wen-ben-yu" aria-hidden="true">¶</a> 文本域</h3>
<p>用于输入多行文本信息，通过将 <code v-pre="">type</code> 属性的值指定为 <code v-pre="">textarea</code>。</p>
<demo-block class="demo-box">
                      <div class="source" slot="source"><py-input type="textarea" v-model="textarea" :rows="2" placeholder="请输入内容">
</py-input>


</div>
                      <div class="description"><p>文本域高度可通过 <code>rows</code> 属性控制，<code>rows</code>的默认值为 5。</p>
</div>
                      <div class="highlight" slot="highlight">
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">py-input</span>
  <span class="hljs-attr">type</span>=<span class="hljs-string">"textarea"</span>
  <span class="hljs-attr">v-model</span>=<span class="hljs-string">"textarea"</span>
  <span class="hljs-attr">:rows</span>=<span class="hljs-string">"2"</span>
  <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span>
&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">py-input</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">textarea</span>: <span class="hljs-string">''</span>,
    };
  },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="wen-ben-yu-2"><a class="header-anchor" href="#wen-ben-yu-2" aria-hidden="true">¶</a> 文本域</h3>
<p>用于输入多行文本信息，通过将 <code v-pre="">type</code> 属性的值指定为 <code v-pre="">textarea</code>。</p>
<demo-block class="demo-box">
                      <div class="source" slot="source"><py-input type="textarea" v-model="textarea" :rows="2" placeholder="请输入内容">
</py-input>


</div>
                      <div class="description"><p>文本域高度可通过 <code>rows</code> 属性控制，<code>rows</code>的默认值为 5。</p>
</div>
                      <div class="highlight" slot="highlight">
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">py-input</span>
  <span class="hljs-attr">type</span>=<span class="hljs-string">"textarea"</span>
  <span class="hljs-attr">v-model</span>=<span class="hljs-string">"textarea"</span>
  <span class="hljs-attr">:rows</span>=<span class="hljs-string">"2"</span>
  <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span>
&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">py-input</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">textarea</span>: <span class="hljs-string">''</span>,
    };
  },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
</section></template>

<script>
export default {
  data() {
    return {
      input: '',
    };
  },
};
</script>