<template><section><h2 id="loadingbar-jia-zai-jin-du-tiao"><a class="header-anchor" href="#loadingbar-jia-zai-jin-du-tiao" aria-hidden="true">¶</a> loadingBar 加载进度条</h2>
<h3 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true">¶</a> 基础用法</h3>
<demo-block class="demo-box">
                      <div class="source" slot="source"><template>
  <py-button :plain="true" @click="start">开始</py-button>
  <py-button :plain="true" @click="finish">关闭</py-button>
  <py-button :plain="true" @click="error">错误</py-button>
  <py-button :plain="true" @click="update">更新进度到66</py-button>
  <py-button :plain="true" @click="destory">销毁</py-button>
</template>


</div>
                      <div class="description"><p>loading</p>
</div>
                      <div class="highlight" slot="highlight">
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">py-button</span> <span class="hljs-attr">:plain</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"start"</span>&gt;</span>开始<span class="hljs-tag">&lt;/<span class="hljs-name">py-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">py-button</span> <span class="hljs-attr">:plain</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"finish"</span>&gt;</span>关闭<span class="hljs-tag">&lt;/<span class="hljs-name">py-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">py-button</span> <span class="hljs-attr">:plain</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"error"</span>&gt;</span>错误<span class="hljs-tag">&lt;/<span class="hljs-name">py-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">py-button</span> <span class="hljs-attr">:plain</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"update"</span>&gt;</span>更新进度到66<span class="hljs-tag">&lt;/<span class="hljs-name">py-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">py-button</span> <span class="hljs-attr">:plain</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"destory"</span>&gt;</span>销毁<span class="hljs-tag">&lt;/<span class="hljs-name">py-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
      start() {
        <span class="hljs-keyword">this</span>.$loading.start();
      },
      finish() {
        <span class="hljs-keyword">this</span>.$loading.finish();
      },
      error() {
        <span class="hljs-keyword">this</span>.$loading.error();
      },
      update() {
        <span class="hljs-keyword">this</span>.$loading.update(<span class="hljs-number">66</span>);
      },
      destory() {
        <span class="hljs-keyword">this</span>.$loading.destory();
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
</section></template>

<script>
  export default {
    methods: {
      start() {
        this.$loading.start();
      },
      finish() {
        this.$loading.finish();
      },
      error() {
        this.$loading.error();
      },
      update() {
        this.$loading.update(66);
      },
      destory() {
        this.$loading.destory();
      }
    }
  }
